/* src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

/* Apply global styles */
body {
  @apply font-poppins bg-earthyGreen-50 text-earthyBrown;
}

/* Custom utility classes */
.motivational-quote {
  @apply text-earthyBrown italic text-center text-lg md:text-xl py-8 px-4;
}

.btn-farm-primary {
  @apply bg-earthyGreen text-white px-6 py-2 rounded-lg 
         hover:bg-earthyGreen-600 transition-all duration-300 
         focus:outline-none focus:ring-2 focus:ring-earthyGreen 
         focus:ring-opacity-50 disabled:opacity-50 disabled:cursor-not-allowed;
}

.map-container {
  @apply h-[400px] w-full rounded-xl overflow-hidden shadow-lg;
}

/* Custom loading spinner */
.loading-spinner {
  @apply w-12 h-12 border-4 border-earthyGreen border-t-transparent 
         rounded-full animate-spin;
}

/* Form input styling */
.input-field {
  @apply w-full p-3 border border-earthyBrown rounded-lg 
         focus:outline-none focus:ring-2 focus:ring-earthyGreen 
         placeholder-earthyBrown/50;
}

/* Product card hover effect */
.product-card {
  @apply bg-white rounded-xl shadow-md overflow-hidden 
         transition-transform duration-300 hover:scale-105;
}

/* Price tag styling */
.price-tag {
  @apply text-earthyGreen font-bold text-xl 
         bg-earthyYellow bg-opacity-20 px-4 py-2 rounded-full;
}

/* Navigation link hover effect */
.nav-link {
  @apply hover:text-earthyYellow transition-colors duration-200;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  @apply w-2;
}

::-webkit-scrollbar-track {
  @apply bg-earthyGreen-100;
}

::-webkit-scrollbar-thumb {
  @apply bg-earthyGreen rounded-full hover:bg-earthyGreen-600;
}

/* Animation for alert messages */
@keyframes slideIn {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.alert-message {
  @apply fixed top-0 left-0 right-0 bg-earthyYellow text-earthyBrown 
         p-4 text-center font-semibold shadow-md z-50;
  animation: slideIn 0.3s ease-out;
}

/* Additional utility classes */
.disabled {
  @apply opacity-50 cursor-not-allowed;
}

/* Responsive text sizes */
.text-responsive {
  @apply text-base md:text-lg lg:text-xl;
}

/* Custom focus styles */
.focus-ring {
  @apply focus:outline-none focus:ring-2 focus:ring-earthyGreen focus:ring-opacity-50;
}